export function formatReadingTime(minutes) {
  let cups = Math.round(minutes / 5)
  // let bowls = 0
  if (cups > 5) {
    return `${new Array(Math.round(cups / Math.E))
      .fill("üç±")
      .join("")} ${minutes} min read`
  } else {
    return `${new Array(cups || 1).fill("‚òïÔ∏è").join("")} ${minutes} min read`
  }
}
